.header {
    margin: 0 30px;
    display: flex;
    align-items: start;
    justify-content: space-between;
    padding-top: 15px;
    padding-bottom: 12px;



    @media (min-width: 768px) {
        margin: 0 50px;
        padding-top: 37px;
    }

    @media (min-width: 1600px) {
        margin: 0 80px;
        padding-top: 86px;
        padding-left: 18px;
    }

    &-inner{
        margin-left: 16px;
        margin-right: 18px;

        @media (min-width: 768px) {
            padding-top: 29px;
            margin-left: 40px;
            margin-right: 40px;
            margin-bottom: 24px;
        }
    }

}

.header__menu {
    @include hover;
    background-color: transparent;
    width: 37px;
    height: 37px;
    position: relative;
    border: none;
    opacity: 0.6;

    &::after {
        display: block;
        content: '';
        width: 100%;
        height: 2px;
        background-color: #fff;
        position: absolute;
        top: 20px;
        left: 0;
    }

    &::before {
        display: block;
        content: '';
        width: 100%;
        height: 2px;
        background-color: #fff;
        position: absolute;
        top: 10px;
        left: 0;
    }

    @media (min-width: 1600px) {
        width: 100px;
        padding-top: 20px;
        padding-left: 41px;
        &::after{
            width: 25px;
            top: 34px;
        }

        &::before {
            width: 25px;
            top: 27px;
        
        }


    }

}

.header__menu-text {
    @include hover;
    @include font;
    display: none;

    @media (min-width: 1600px) {
        
        display: block;
        font-size: 20px;
        line-height: 22px;
        text-align: right;
        letter-spacing: 0.06em;        
        text-transform: uppercase;
    }
}

.header__logo {
    @include hover;
    @media (min-width: 768px) {
        width: 155px;
    }

}